import { Component, ElementRef, Injector, Input, ViewChild } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { ActivatedRouteSnapshot } from '@angular/router';
import { GridComponent } from 'src/app/components/grid/grid.component';
import { InputSearchComponent } from 'src/app/components/input/input-search/input-search.component';
import { InputSelectComponent } from 'src/app/components/input/input-select/input-select.component';
import { ToolbarButton } from 'src/app/components/toolbar/toolbar.component';
import { DemandaDaoService } from 'src/app/dao/demanda-dao.service';
import { UnidadeDaoService } from 'src/app/dao/unidade-dao.service';
import { UsuarioDaoService } from 'src/app/dao/usuario-dao.service';
import { ListenerAllPagesService } from 'src/app/listeners/listener-all-pages.service';
import { Afastamento } from 'src/app/models/afastamento.model';
import { Comentario } from 'src/app/models/comentario';
import { Demanda, DemandaChecklist } from 'src/app/models/demanda.model';
import { Plano } from 'src/app/models/plano.model';
import { Usuario } from 'src/app/models/usuario.model';
import { PageListBase } from 'src/app/modules/base/page-list-base';
import { CalendarService, FeriadoList } from 'src/app/services/calendar.service';
import { LookupItem } from 'src/app/services/lookup.service';

export type StatusDemanda = {
  text: string,
  title?: string,
  icon: string,
  class: string
}

export type ExtraDemanda = {
  planos: {[plano_id: string]: Plano},
  afastamentos: {[usuario_id: string]: Afastamento[]},
  feriados?: {[unidade_id: string]: FeriadoList}
}

@Component({
  selector: 'demanda-list-grid',
  templateUrl: './demanda-list-grid.component.html',
  styleUrls: ['./demanda-list-grid.component.scss']
})
export class DemandaListGridComponent extends PageListBase<Demanda, DemandaDaoService> {
  @ViewChild(GridComponent, { static: false }) public grid?: GridComponent;
  @ViewChild('unidade', { static: false }) public unidade?: InputSearchComponent;
  @ViewChild('usuario', { static: false }) public usuario?: InputSearchComponent;
  @ViewChild('etiqueta', { static: false }) public etiqueta?: InputSelectComponent;
  @Input() snapshot?: ActivatedRouteSnapshot;
  
  public unidadeDao: UnidadeDaoService;
  public usuarioDao: UsuarioDaoService;
  public allPages: ListenerAllPagesService;
  public calendar: CalendarService;
  public extra: ExtraDemanda;
  public formEdit: FormGroup;
  public etiquetas: LookupItem[] = [];
  public checklist?: DemandaChecklist[];

  constructor(public injector: Injector, dao: DemandaDaoService) {
    super(injector, Demanda, DemandaDaoService);
    this.unidadeDao = injector.get<UnidadeDaoService>(UnidadeDaoService);
    this.usuarioDao = injector.get<UsuarioDaoService>(UsuarioDaoService);
    this.allPages = injector.get<ListenerAllPagesService>(ListenerAllPagesService);
    this.calendar = injector.get<CalendarService>(CalendarService);
    this.dao = dao;
    this.join = ["atividade", "demandante", "pausas", "usuario", "unidade", "comentarios.usuario:id,nome,apelido"];
    /* Inicializações */
    this.title = "Demandas";
    this.extra = { planos: {}, afastamentos: {} };
    this.filter = this.fh.FormBuilder({
      atribuidas_para_mim: {default: false},
      usuario_id: {default: ""},
      somente_unidade_atual: {default: false},
      unidades_subordinadas: {default: false},
      unidade_id: {default: ""}
    });
    this.formEdit = this.fh.FormBuilder({
      etiquetas: {default: []},
      etiqueta: {default: ""}
    });
    this.groupBy = [{field: "unidade.sigla", label: "Unidade"}];
  }

  public orderComentarios(comentarios?: Comentario[]) {
    return comentarios?.sort((a: Comentario, b: Comentario) => {
      return (a.path + "/" + a.id) < (b.path + "/" + b.id) || (a.path == b.path && a.data_hora.getTime() < b.data_hora.getTime()) ? -1 : 1;
    }) || [];
  }

  public onGridLoad(rows: any[]) {
    /* Ordena os comentários */
    rows?.forEach((demanda: Demanda) => {
      demanda.comentarios = this.orderComentarios(demanda.comentarios);
    });
    /* Recebe informações extra da query para auxiliar em cálculos e melhorar performace da consulta */
    const extra = this.grid!.query?.extra;
    if(extra) {
      this.extra.planos = Object.assign(this.extra.planos, extra.planos || {});
      for(let [key, value] of Object.entries(extra.afastamentos || {})) {
        this.extra.afastamentos[key] = (value as Array<Afastamento>).reduce((a, v) => {
          if(!a.find(x => x.id == v.id)) a.push(v);
          return a;
        }, this.extra.afastamentos[key] || []);
      }
      Object.entries(extra.feriados || {}).forEach(([key, value]) => {
        if(!this.calendar.feriadosCadastrados[key]) this.calendar.feriadosCadastrados[key] = value as FeriadoList; 
      });
      this.cdRef.detectChanges();    
    }
  }

  public modalRefreshId(demanda: Demanda) {
    return { modal: true, modalClose: (modalResult?: string) => this.grid!.query!.refreshId(demanda.id) };
  }

  public comentarioClick(element: HTMLSpanElement) {
    const value = element.getAttribute("data-expanded");
    element.setAttribute("data-expanded", value == "true" ? "false" : "true");
  }

  public temposDemanda(row: Demanda): StatusDemanda[] {
    /* Atualiza somente a cada mudança de minuto da unidade atual */
    if(row.metadados && row.metadados.extra?.lastUpdate != this.auth.unidadeHora) {
      let tempos = [
        { class: "badge bg-light text-dark", title: "Prazo de entrega", icon: "bi bi-calendar-check", text: this.dao!.getDateTimeFormatted(row.prazo_entrega) },
        { class: "badge bg-light text-dark", title: "Tempo pactuado", icon: "fas fa-handshake", text: (row.tempo_pactuado ? this.util.decimalToTimerFormated(row.tempo_pactuado, true) : "Não") + " pactuado" }
      ];
      if(row.metadados.concluido) tempos.push({ class: "badge bg-light text-dark", title: "Data de entrega realizada", icon: "bi bi-check-circle", text: this.dao!.getDateTimeFormatted(row.data_entrega) });
      if(row.metadados.iniciado) {
        const cargaHoraria = this.extra?.planos[row.plano_id!]?.carga_horaria || 0;
        const afastamentos = this.extra?.afastamentos[row.usuario_id!] || [];
        const despendido = row.metadados.concluido ? (row.tempo_despendido || 0) : this.calendar.horasUteis(row.data_inicio!, this.auth.hora, cargaHoraria, row.unidade!, "ENTREGA", row.pausas, afastamentos);
        tempos.push({ class: "badge " + (despendido > row.tempo_pactuado ? "bg-warning" : "bg-light") + " text-dark", title: "Tempo despendido", icon: "bi bi-hourglass-split", text: this.util.decimalToTimerFormated(despendido, true) + " despendido" });
      }
      if(!row.metadados.concluido && row.prazo_entrega.getTime() < this.auth.hora.getTime()) {
        const atrasado = this.calendar.horasAtraso(row.prazo_entrega, row.unidade!);
        tempos.push({ class: "badge bg-danger", title: "Tempo de atraso", icon: "bi bi-alarm", text: this.util.decimalToTimerFormated(atrasado, true) + " atrasado" });
      } 
      row.metadados.extra = row.metadados.extra || {};
      row.metadados.extra.lastUpdate = this.auth.unidadeHora;
      row.metadados.extra.tempos = tempos;
    }
    return row.metadados?.extra?.tempos || [];
  }

  public dynamicOptions(row: any): ToolbarButton[] {
    let result: ToolbarButton[] = [];
    let demanda: Demanda = row as Demanda;
    
    result.push({label: "Informações", icon: "bi bi-info-circle", onClick: (demanda: Demanda) => this.go.navigate({route: ['gestao', 'demanda', demanda.id, 'consult']}, {modal: true})});  
    if(!demanda.metadados?.iniciado) { /* Não iniciado */
      result.push(
        {label: "Iniciar", icon: "bi bi-play-circle", onClick: (demanda: Demanda) => this.go.navigate({route: ['gestao', 'demanda', demanda.id, 'iniciar']}, this.modalRefreshId(demanda))},
        {label: "Alterar demanda", icon: "bi bi-pencil-square", onClick: (demanda: Demanda) => this.go.navigate({route: ['gestao', 'demanda', demanda.id, 'edit']}, this.modalRefreshId(demanda))},
        {divider: true},
        {label: "Excluir demanda", icon: "bi bi-trash", onClick: this.delete.bind(this)}
      );
    } else if(demanda.metadados?.avaliado) { /* Avaliado */
      result.push(
        {label: "Alterar avaliação", icon: "bi bi-star-half", onClick: (demanda: Demanda) => this.go.navigate({route: ['gestao', 'demanda', demanda.id, 'avaliar']}, this.modalRefreshId(demanda))}, 
        {divider: true},
        {label: "Cancelar avaliação", icon: "bi bi-backspace", onClick: this.cancelarAvaliacao.bind(this)}
      );  
    } else if(demanda.metadados?.concluido) { /* Concluído */
      result.push(
        {label: "Editar conclusão", icon: "bi bi-check-circle", onClick: (demanda: Demanda) => this.go.navigate({route: ['gestao', 'demanda', demanda.id, 'concluir']}, this.modalRefreshId(demanda))}, 
        {label: "Avaliar", icon: "bi bi-star-half", onClick: (demanda: Demanda) => this.go.navigate({route: ['gestao', 'demanda', demanda.id, 'avaliar']}, this.modalRefreshId(demanda))},
        {divider: true},
        {label: "Cancelar conclusão", icon: "bi bi-backspace", onClick: this.cancelarConclusao.bind(this)}
      );  
    } else if(demanda.metadados?.iniciado) { /* Iniciado */
      if(demanda.metadados?.suspenso) { /* Iniciada e Suspensa */
        result.push({label: "Reiniciar", icon: "bi bi-play-circle", onClick: (demanda: Demanda) => this.go.navigate({route: ['gestao', 'demanda', demanda.id, 'pausar'], params: {reiniciar: true}}, this.modalRefreshId(demanda))});  
      } else { /* Iniciada e não Suspensa */
        result.push(
          {label: "Concluir", icon: "bi bi-check-circle", onClick: (demanda: Demanda) => this.go.navigate({route: ['gestao', 'demanda', demanda.id, 'concluir']}, this.modalRefreshId(demanda))}, 
          {label: "Prorrogar prazo", icon: "bi bi-skip-end-circle", onClick: (demanda: Demanda) => this.go.navigate({route: ['gestao', 'demanda', demanda.id, 'prorrogar']}, this.modalRefreshId(demanda))}, 
          {label: "Editar inicio", icon: "bi bi-play-circle", onClick: (demanda: Demanda) => this.go.navigate({route: ['gestao', 'demanda', demanda.id, 'iniciar']}, this.modalRefreshId(demanda))}, 
          {label: "Suspender", icon: "bi bi-pause-circle", onClick: (demanda: Demanda) => this.go.navigate({route: ['gestao', 'demanda', demanda.id, 'pausar']}, this.modalRefreshId(demanda))},
          {divider: true},  
          {label: "Cancelar inicio", icon: "bi bi-backspace", onClick: this.cancelarInicio.bind(this)}
        );
      }
    }
    return result;
  }

  public cancelarInicio(demanda: Demanda) {
    const self = this;
    this.dialog.confirm("Cancelar inicio ?", "Deseja realmente cancelar a inicialização?").then(confirm => {
      if(confirm) {
        this.dao!.cancelarInicio(demanda.id).then(function () {
          self.grid!.query!.refreshId(demanda.id);
          self.dialog.alert("Sucesso", "Cancelado com sucesso!");
        }).catch(function (error) {
          self.dialog.alert("Erro", "Erro ao cancelar inicio: " + error?.message ? error?.message : error);
        });
      }
    }); 
  }

  public cancelarConclusao(demanda: Demanda) {
    const self = this;
    this.dialog.confirm("Cancelar conclusão ?", "Deseja realmente cancelar a conclusão?").then(confirm => {
      if(confirm) {
        this.dao!.cancelarConclusao(demanda.id).then(function () {
          self.grid!.query!.refreshId(demanda.id);
          self.dialog.alert("Sucesso", "Cancelado com sucesso!");
        }).catch(function (error) {
          self.dialog.alert("Erro", "Erro ao cancelar conclusão: " + error?.message ? error?.message : error);
        });
      }
    }); 
  }

  public cancelarAvaliacao(demanda: Demanda) {
    const self = this;
    this.dialog.confirm("Cancelar avaliacao ?", "Deseja realmente cancelar a avaliacao?").then(confirm => {
      if(confirm) {
        this.dao!.cancelarAvaliacao(demanda.id).then(function () {
          self.grid!.query!.refreshId(demanda.id);
          self.dialog.alert("Sucesso", "Cancelado com sucesso!");
        }).catch(function (error) {
          self.dialog.alert("Erro", "Erro ao cancelar avaliacao: " + error?.message ? error?.message : error);
        });
      }
    }); 
  }

  public dynamicButtons(row: any): ToolbarButton[] {
    let result: ToolbarButton[] = [];
    let demanda: Demanda = row as Demanda;

    if(!demanda.metadados?.iniciado) { /* Não iniciado */
      result.push({hint: "Iniciar", icon: "bi bi-play-circle", color: "btn-outline-secondary", onClick: (demanda: Demanda) => this.go.navigate({route: ['gestao', 'demanda', demanda.id, 'iniciar']}, this.modalRefreshId(demanda))});  
    } else if(demanda.metadados?.avaliado) { /* Avaliado */
      result.push({hint: "Alterar avaliação", icon: "bi bi-check-all", color: "btn-outline-danger", onClick: (demanda: Demanda) => this.go.navigate({route: ['gestao', 'demanda', demanda.id, 'avaliar', 'edit']}, this.modalRefreshId(demanda))});  
    } else if(demanda.metadados?.concluido) { /* Concluído */
      result.push({hint: "Avaliar", icon: "bi bi-star-half", color: "btn-outline-success", onClick: (demanda: Demanda) => this.go.navigate({route: ['gestao', 'demanda', demanda.id, 'avaliar']}, this.modalRefreshId(demanda))});  
    } else if(demanda.metadados?.iniciado) { /* Iniciado */
      if(demanda.metadados?.suspenso) { /* Iniciada e Suspensa */
        result.push({hint: "Reiniciar", icon: "bi bi-play-circle", color: "btn-outline-secondary", onClick: (demanda: Demanda) => this.go.navigate({route: ['gestao', 'demanda', demanda.id, 'pausar'], params: {reiniciar: true}}, this.modalRefreshId(demanda))});  
      } else { /* Iniciada e não Suspensa */
        result.push({hint: "Concluir", icon: "bi bi-check", color: "btn-outline-success", onClick: (demanda: Demanda) => this.go.navigate({route: ['gestao', 'demanda', demanda.id, 'concluir']}, this.modalRefreshId(demanda))});
      }
    }
    return result;
  }

  public getStatus(row: any): StatusDemanda[] {
    let demanda: Demanda = row as Demanda;
    let result: StatusDemanda[] = [];
    switch (demanda.metadados?.status) {
      case "AVALIADO": result.push({text: "Avaliado", icon: "bi bi-star-half", class: "badge rounded-pill bg-success"}); break;
      case "CONCLUIDO": result.push({text: "Concluído", icon: "bi bi bi-check-circle", class: "badge rounded-pill bg-primary"}); break;
      case "INICIADO": result.push({text: "Iniciado", icon: "bi bi-play-circle", class: "badge rounded-pill bg-info text-dark"}); break;
      case "LANCADO": result.push({text: "Não iniciado", icon: "bi bi-stop-circle", class: "badge rounded-pill bg-warning text-dark"}); break;
      default: result.push({text: "Desconhecido", icon: "bi bi-question-circle", class: "badge rounded-pill bg-light text-dark"});
    }
    if(demanda.metadados?.atrasado) result.push({text: "Atrasado", icon: "bi bi-alarm", class: "badge rounded-pill bg-danger"});
    if(demanda.metadados?.suspenso) result.push({text: "Suspenso", icon: "bi bi-pause-circle", class: "badge rounded-pill bg-warning text-dark"});
    return result;
  }

  public getEtiquetaStyle(etiqueta: any) {
    const bgColor = etiqueta.color || "#000000";
    const txtColor = this.util.contrastColor(bgColor);
    return `background-color: ${bgColor}; color: ${txtColor};`;
  }

  public onAtribuidasParaMimChange(event: Event) {
    if(this.filter!.controls.atribuidas_para_mim.value) {
      this.filter!.controls.usuario_id.setValue(this.auth.usuario!.id);
    } else {
      this.filter!.controls.usuario_id.setValue(undefined);
    }
  }

  public onSomenteUnidadeAtualChange(event: Event) {
    if(this.filter!.controls.somente_unidade_atual.value) {
      this.filter!.controls.unidade_id.setValue(this.auth.unidade!.id);
    } else {
      this.filter!.controls.unidade_id.setValue(undefined);
    }
  }

  public onProcessoClick(row: any) {
    this.allPages.openDocumentoSei(row.id_processo, row.id_requisicao);
  }

  public async onColumnEtiquetasSave(row: any) {
    try {
      const saved = await this.dao!.update(row.id, {
        etiquetas: this.formEdit.controls.etiquetas.value
      });
      return !!saved;
    } catch (error) {
      return false;
    }
  }

  public async onColumnEtiquetasEdit(row: any) {
    this.formEdit.controls.etiquetas.setValue(row.etiquetas);
    this.formEdit.controls.etiqueta.setValue("");
    this.etiquetas = this.util.merge(row.atividade?.etiquetas_predefinidas, row.unidade?.etiquetas, (a, b) => a.key == b.key); 
  }

  public async onColumnChecklistSave(row: any) {
    try {
      const saved = await this.dao!.update(row.id, {
        checklist: this.checklist
      });
      row.checklist = this.checklist;
      return !!saved;
    } catch (error) {
      return false;
    }
  }

  public async onColumnChecklistEdit(row: any) {
    this.checklist = this.util.clone(row.checklist);
  }

  public filterWhere = (filter: FormGroup) => {
    let result: any[] = [];
    let form: any = filter.value;
 
    if(form.usuario_id?.length) {
      result.push(["usuario_id", "==", form.usuario_id]);
    } 
    if(form.unidade_id?.length) {
      result.push(["unidade_id", "==", form.unidade_id]);
    }
    if(form.unidades_subordinadas) {
      result.push(["unidades_subordinadas", "==", true]);
    }
    return result;
  }

  public addItemHandleEtiquetas(): LookupItem | undefined {
    let result = undefined;
    if(this.etiqueta && this.etiqueta.selectedItem) {
      const item = this.etiqueta.selectedItem;
      const key = item.key?.length ? item.key : this.util.md5(item.value);
      if(this.util.validateLookupItem(this.formEdit.controls.etiquetas.value, key)) {
        result = {
          key: key,
          value: item.value,
          color: item.color,
          icon: item.icon
        };
        this.formEdit.controls.etiqueta.setValue("");
      }
    }
    return result;
  };

}

